import React, { FC } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Grid from '@material-ui/core/Grid';
import { makeStyles, Theme } from '@material-ui/core/styles';
import clsx from 'clsx';

import { IConfirmationModal } from './IConfirmationModal';

const useStyles = makeStyles((theme: Theme) => ({
	dialog: {},
	dialogAction: {
		marginBottom: theme.spacing(2),
	},
}));

export const ConfirmationModal: FC<IConfirmationModal.IProps> = props => {
	const classes = useStyles();
	const reset = () => {};

	const handleCancel = () => {
		reset();
		props.onClose(true);
	}

	const handleClose = () => {
		reset();
		props.onClose();
	};

	const handleSubmit = () => {
		reset();
		props.onSubmit();
		props.onClose();
	};

	return (
		<Dialog
			open={props.isModalOpen}
			onClose={handleClose}
			aria-labelledby="form-dialog-title"
			maxWidth="sm"
			className={classes.dialog}
		>
			<DialogTitle id="form-dialog-title">{props.title}</DialogTitle>
			<DialogContent>
				<Grid container spacing={2} justify="space-between" alignItems="flex-start">
					<Grid item xs={12}>
						{props.message}
					</Grid>
				</Grid>
			</DialogContent>
			<DialogActions className={clsx(classes.dialogAction)}>
				<Grid container spacing={2} justify="center" alignItems="flex-start">
					<Grid item xs={4}>
						<Button onClick={handleCancel} variant="outlined" color="default" fullWidth>
							Cancel
						</Button>
					</Grid>
					<Grid item xs={4}>
						<Button
							onClick={handleSubmit}
							variant="contained"
							color="primary"
							fullWidth
						>
							{props.submitType ? props.submitType : 'Confirm'}
						</Button>
					</Grid>
				</Grid>
			</DialogActions>
		</Dialog>
	);
};

ConfirmationModal.defaultProps = {
	isModalOpen: false,
};

export default ConfirmationModal;