const Select = ({ id, value, items }: any) => {
  const cellSelectClasses = cellSelectStyles();
  return (
    <MaterialUI.Select
      id={id}
      classes={cellSelectClasses}
      value={value}
      style={{ width: '100%', backgroundColor: 'transparent' }}
      renderValue={(selected: any) => {
        if (selected !== null) {
          const value: any = items.find((item: any) => {
            return item.value === selected;
          });
          if (value) {
            return value.name;
          } else {
            return '';
          }
        }
        return '';
      }}
    >
      {items.map((item: any) => (
        <MaterialUI.MenuItem key={item.value} value={item.value}>
          {item.name}
        </MaterialUI.MenuItem>
      ))}
    </MaterialUI.Select>
  );
};

const StatusSelect = ({ items, ...restProps }: any) => {
  return (
    <MaterialUI.Select
      variant='outlined'
      style={{ width: '100%' }}
      renderValue={(selected: any) => {
        if (selected != null) {
          const value: any = items.find((item: any) => {
            return item.value === selected;
          });
          if (value) {
            return value.label;
          } else {
            return '';
          }
        }
        return '';
      }}
      {...restProps}
    >
      {items.map((item: any, i: number) => (
        <MaterialUI.MenuItem key={i} value={item.value}>
          {item.label}
        </MaterialUI.MenuItem>
      ))}
    </MaterialUI.Select>
  );
};


const TableFilterCell = ({
  filter,
  onFilter,
  filteringEnabled,
  classes, column
}: any) => {
  if (filteringEnabled) {
    if (column['datePicker']) {
      return (
        <MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px' }} classes={classes}>
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <KeyboardDatePicker
              // inputProps={{
              //   disabled: true
              // }}
              variant="dialog"
              format="MMMM D, YYYY"
              inputVariant="outlined"
              value={filter ? filter.value : ''}
              invalidDateMessage=""
              onChange={date => { console.log(date); onFilter(date ? { value: date.toISOString() } : null) }}
            />
          </MuiPickersUtilsProvider>
        </MaterialUI.TableCell>
      )
    } else if (column['staticDropdown']) {
      return (
        <MaterialUI.TableCell style={{ paddingTop: '5px' }}>
          <StatusSelect
            onChange={e => onFilter(e.target.value ? { value: e.target.value } : null)}
            name={'status'}
            items={column['staticDropdownOptions'] ? column['staticDropdownOptions'] : []}
            id={'status'}
            value={filter ? filter.value : ''}
          />
        </MaterialUI.TableCell>
      )
    } else {
      return (
        <MaterialUI.TableCell
          style={{ padding: '0px 10px 8px 10px' }}
          classes={classes}
        >
          <MaterialUI.OutlinedInput
            classes={classes}
            style={{ width: '100%' }}
            value={filter ? filter.value : ''}
            labelWidth={0}
            onChange={e =>
              onFilter(e.target.value ? { value: e.target.value } : null)
            }
            placeholder=""
          />
        </MaterialUI.TableCell>
      );
    }
  } else {
    // return null;
    if (column['actionColumn']) {
      return (
        <MaterialUI.TableCell
          style={{ padding: '0px 0px 0px 0px' }}
          classes={classes}
          align="center"
        >
          <MaterialUI.Checkbox
            color='primary'
            onChange={e => {
              onFilter(e.target.checked !== undefined ? { 'action': e.target.checked } : null);
            }}
          />
        </MaterialUI.TableCell>

      );
    } else if (column['resetColumn']) {
      return (
        <MaterialUI.TableCell align="center">
          <MaterialUI.Grid container justify="center" style={{ paddingLeft: '22px' }}>
            <Link onClick={() => {
              onFilter({ 'reset_action': true });
            }}>
              <MaterialUI.Grid container justify="center" alignItems="center">
                <MaterialUI.Grid item xs={12} alignContent="center">
                  <FontAwesomeIcon icon={faRedoAlt} style={{ fontSize: '20px' }} />
                </MaterialUI.Grid>
                <MaterialUI.Grid item xs={12} alignContent="center">
                  <span style={{ fontSize: '10px' }}>Reset All</span>
                </MaterialUI.Grid>
              </MaterialUI.Grid>
            </Link>
          </MaterialUI.Grid>
        </MaterialUI.TableCell>
      );
    } else {
      return (
        <MaterialUI.TableCell
          style={{ padding: '0px 10px 8px 10px' }}
          classes={classes}
        ></MaterialUI.TableCell>
      );
    }

  }
};

