const RequiredFormatter = ({ row, value }: any) => {
  if (
    row['required'] === true &&
    (value === '' || value === undefined || value === null)
  ) {
    return (
      <FontAwesomeIcon
        style={{ color: '#FD5451', fontSize: '16px' }}
        icon={faExclamationCircle}
      />
    );
  } else if (
    row['required'] === false &&
    (value === '' || value === undefined || value === null)
  ) {
    return 'N/A';
  } else {
    return value;
  }
};

const RequiredDataTypeProvider = (props: any) => (
  <DataTypeProvider formatterComponent={RequiredFormatter} {...props} />
);

