interface TableComponentProps {
  rows: any[];
  columns: any[];
  columnFilteringEnabled?: boolean;
  columnExtensions?: any[];
  filterExtensions?: any[];
  formattedColumns?: any;
  columnBands?: any[] | null;
  singleSelect?: boolean;
  disableSearch?: boolean;
  disablePaging?: boolean;
  disableSelect?: boolean;
  disableFilter?: boolean;
  disableSort?: boolean;
  showSelectionColumn?: boolean;
  toolbarType?: string | null;
  topBar?: () => JSX.Element;
  id: string;
  totalCount: number;
  message: string;
  defaultFilter?: any[];
  update: (skip: number, limit: number) => void;
  updateSorting: (sort: object) => void;
  onClickRow?: (row: number[]) => void;
  onSelectedRadio?: (row: number, value: string) => void;
  onFilterChange?: (response: any) => void;
  onSortingChange?: (response: any) => void;
  onClickCell?: (row: any) => void;
  onPageChange: (currentPage: any, skip: any) => void;
  ongPageSizeChange: (value: number) => void;
  onSelectedReset?: (row: any) => void;
  defaultSort?: any[];
  query?: string;
  currentPage: number;
  limit: number;
  pageSizes?: number[];
  approveDisapproveItems?: any[];
  disableIntegratedFiltering?: boolean;
  isVirtualTable?: boolean;
  disableIntegratedSorting?: boolean;
}

export const TableComponent: React.FC<TableComponentProps> = (
  props: TableComponentProps,
): JSX.Element => {
  const {
    disableIntegratedFiltering,
    columns,
    formattedColumns,
    singleSelect,
    disablePaging,
    disableSearch,
    disableSelect,
    disableSort,
    disableFilter,
    toolbarType,
    columnExtensions,
    columnBands,
    id,
    showSelectionColumn,
    query,
    totalCount,
    currentPage,
    limit,
    disableIntegratedSorting,
  } = props;
  const [selection, setSelection] = useState<number[]>([]);
  const [pageSizes] = useState(props.pageSizes ? props.pageSizes : [10, 25, 50, 100, 0]);
  const [sorting, setSorting] = useState(props.defaultSort ? props.defaultSort : []);
  const [rows, setRows] = useState(props.rows);
  useEffect(() => {
    setRows(props.rows);
  }, [props.rows]);

  useEffect(() => {
    const empty: any = [];
    setSorting(props.defaultSort ? props.defaultSort : empty);
  }, [props.defaultSort])

  // useEffect(() => {
  //   global_approve_disapprove = props.approveDisapproveItems;
  //   setRows([...rows])
  // }, [props.approveDisapproveItems]);

  const dataTypeProviders: any[] = [];

  const changePageSize = (value) => {
    // props.update(currentPage, value);
    props.ongPageSizeChange(value);
  }

  const setCurrentPageMethod = (page) => {
    let skip = (limit * page);

    props.onPageChange(page, skip);
  }

  const setSortingMethod = (e) => {
    props.updateSorting(e);
    setSorting(e);
    // setRows([]);
  }

  function handleSelectionChange(newSelection: any[]) {
    console.log('here click row');
    if (props.onClickRow) {
      // selectedRow = newSelection[0];
      if (!radioSelected && !clickCell && !rowResetSelected) {
        console.log('onclickrow');
        props.onClickRow(newSelection);
      } else if (radioSelected && !rowResetSelected) {
        console.log('radioselected', newSelection);
        if (props.onSelectedRadio)
          props.onSelectedRadio(newSelection[0], selectedValue);
        setRows([...rows]);
        radioSelected = false;
      } else if (rowResetSelected) {
        if (props.onSelectedReset) props.onSelectedReset(newSelection);
        rowResetSelected = false;
      } else if (clickCell) {
        console.log(' here !!! >>>', props.onClickCell);
        if (props.onClickCell) props.onClickCell(clickValue);
        clickCell = false;
        clickValue = {};
      }
    } else {
      if (singleSelect) {
        const lastSelected = newSelection.find(
          (selected: number) => selection.indexOf(selected) === -1,
        );

        console.log(newSelection, selection, lastSelected);
        if (lastSelected !== undefined) {
          setSelection([lastSelected]);
        }
      } else {
        setSelection(newSelection);
      }
    }
  }
  if (
    formattedColumns !== undefined &&
    Object.keys(formattedColumns).length > 0
  ) {
    if (formattedColumns['number'] !== undefined) {
      dataTypeProviders.push(
        <NumberDataTypeProvider for={formattedColumns.number} />,
      );
    }
    if (formattedColumns['text'] !== undefined) {
      dataTypeProviders.push(
        <TextDataTypeProvider for={formattedColumns.text} />,
      );
    }
    if (formattedColumns['system_name'] !== undefined) {
      dataTypeProviders.push(
        <SystemNameDataTypeProvider for={formattedColumns.system_name} />,
      );
    }
    if (formattedColumns['data_name'] !== undefined) {
      dataTypeProviders.push(
        <DataDataTypeProvider for={formattedColumns.data_name} />,
      );
    }
    if (formattedColumns['value_name'] !== undefined) {
      dataTypeProviders.push(
        <ValuesNameDataTypeProvider for={formattedColumns.value_name} />,
      );
    }
    if (formattedColumns['required_columns'] !== undefined) {
      dataTypeProviders.push(
        <EmptyCellDataTypeProvider for={formattedColumns.required_columns} />,
      );
    }
    if (formattedColumns['radio'] !== undefined) {
      dataTypeProviders.push(
        <RadioDataTypeProvider for={formattedColumns.radio} />,
      );
    }
    if (formattedColumns['incomplete_radio'] !== undefined) {
      dataTypeProviders.push(
        <IncompleteRadioDataTypeProvider
          for={formattedColumns.incomplete_radio}
        />,
      );
    }
    if (formattedColumns['approve_disapprove_action'] !== undefined) {
      dataTypeProviders.push(
        <ApproveDisapproveRadioDataTypeProvider
          for={formattedColumns.approve_disapprove_action}
        />,
      );
    }
    if (formattedColumns['approve_disapprove_action_virtual'] !== undefined) {
      dataTypeProviders.push(
        <ApproveDisapproveRadioDataTypeProviderVirtual
          for={formattedColumns.approve_disapprove_action_virtual}
        />,
      );
    }
    if (formattedColumns['approve_disapprove_reset'] !== undefined) {
      dataTypeProviders.push(
        <ResetDataTypeProvider
          for={formattedColumns.approve_disapprove_reset}
        />,
      );
    }
    if (formattedColumns['required_checkbox'] !== undefined) {
      dataTypeProviders.push(
        <RequiredCheckboxDataTypeProvider
          for={formattedColumns.required_checkbox}
        />,
      );
    }
    if (formattedColumns['upload_file'] !== undefined) {
      dataTypeProviders.push(
        <UploadFileDataTypeProvider
          for={formattedColumns.upload_file}
          {...props}
        />,
      );
    }
    if (formattedColumns['required_cell'] !== undefined) {
      dataTypeProviders.push(
        <RequiredDataTypeProvider
          for={formattedColumns.required_cell}
          {...props}
        />,
      );
    }
    if (formattedColumns['date'] !== undefined) {
      dataTypeProviders.push(
        <DateCellDataTypeProvider for={formattedColumns.date} />,
      );
    }
    if (formattedColumns['time'] !== undefined) {
      dataTypeProviders.push(
        <TimeCellDataTypeProvider for={formattedColumns.time} />,
      );
    }
  }

  let toolbar = DefaultTableToolbar;

  return (
    <Paper id={id}>
      {props.topBar ? props.topBar() : null}
      <Grid rows={rows} columns={columns}>
        {dataTypeProviders}
        {/* Need to initialize paging state before paging panel */}
        {!disablePaging ? (
          <PagingState
            currentPage={currentPage}
            onCurrentPageChange={setCurrentPageMethod}
            pageSize={limit}
            onPageSizeChange={changePageSize}
          />
        ) : null}

        {!disablePaging ? <CustomPaging totalCount={totalCount} /> : null}

        {!disableSearch || query ? <SearchState value={query} /> : null}

        {!disableSort ? (//&& props.defaultSort !== undefined ? (
          <SortingState
            defaultSorting={[]}
            sorting={sorting}
            onSortingChange={setSortingMethod}
          />
        ) : !disableSort ? (
          <SortingState
            defaultSorting={[]}
          />
        ) : null}

        {!disableSort && !disableIntegratedSorting ? <IntegratedSorting /> : null}

        {!disableSelect || !showSelectionColumn ? (
          <SelectionState
            selection={selection}
            onSelectionChange={handleSelectionChange}
          />
        ) : null}
        {!disableSelect ? <IntegratedSelection /> : null}

        {!disableFilter ? (
          <FilteringState
            defaultFilters={[]}
            filters={
              props.defaultFilter !== undefined ? props.defaultFilter : []
            }
            columnFilteringEnabled={props.columnFilteringEnabled}
            columnExtensions={props.filterExtensions}
            onFiltersChange={props.onFilterChange}
          />
        ) : null}

        {!disableFilter && !disableIntegratedFiltering ? <IntegratedFiltering /> : null}

        {!props.isVirtualTable ? (
          <Table
            columnExtensions={columnExtensions}
            messages={{ noData: props.message }}
          />
        ) : null}



        {props.isVirtualTable ? (
          <VirtualTable
            columnExtensions={columnExtensions}
            height={300}
          />
        ) : null}

        {!disableSelect && !showSelectionColumn ? (
          <TableSelection
            showSelectAll={!singleSelect ? true : false}
            cellComponent={!singleSelect ? TableCheckBox : TableRadio}
            headerCellComponent={TableCheckBox}
          />
        ) : !showSelectionColumn ? (
          <TableSelection
            selectByRowClick
            showSelectionColumn={false}
            cellComponent={!singleSelect ? TableCheckBox : TableRadio}
            headerCellComponent={TableCheckBox}
          />
        ) : null}

        <TableHeaderRow
          showSortingControls={!disableSort}
          sortLabelComponent={TableSortLabel}
        />

        {!disableFilter ? (
          <TableFilterRow cellComponent={TableFilterCell} />
        ) : null}
        {columnBands ? <TableBandHeader columnBands={columnBands} /> : null}

        {!disableSearch || toolbarType ? (
          <Toolbar rootComponent={toolbar} />
        ) : null}
        {!disableSearch ? (
          <SearchPanel inputComponent={TableSearchPanel} />
        ) : null}

        {!disablePaging ? <PagingPanel pageSizes={pageSizes} /> : null}
      </Grid>
    </Paper>
  );
};

TableComponent.defaultProps = {
  rows: [],
  columns: [],
  columnExtensions: [],
  filterExtensions: [],
  columnBands: null,
  formattedColumns: {},
  singleSelect: false,
  disableSearch: false,
  disablePaging: false,
  disableSelect: false,
  disableFilter: false,
  disableSort: false,
  toolbarType: null,
  defaultFilter: [],
  showSelectionColumn: false,
  columnFilteringEnabled: true,
  id: 'table-1',
  totalCount: 0,
  message: 'No available data',
  query: '',
  currentPage: 0,
  limit: 0
};
