const titleCase = (title: string) => {
	var sentence: string[] = title.toLowerCase().split(" ");
	for (var i = 0; i < sentence.length; i++) {
		sentence[i] = sentence[i][0].toUpperCase() + sentence[i].slice(1);
	}
	const output: string = sentence.join(" ");
	return output;
}

