export const constructPostIcd10 = (data: any) => {
	let data_arr: Icd10[] = [];
	if (Array.isArray(data)) {
		data_arr = data
	} else {
		data_arr.push(data)
	}
	const payload: Icd10[] = data_arr.map(item => {
		if (item['expiry_date'] === '') {
			delete item['expiry_date'];
		}
		if (item['status'] !== undefined) {
			delete item['status']
		}
		item['gender'] = item['gender_limited'] ? item['gender'] : '';
		return item;
	})
	return payload
}

