export const constructAddSynonymPayload = (full_code: string, title: string, diagnosis_code: string) => {
	const synonym = { full_code: full_code, synonyms: [title], diagnosis_code: diagnosis_code }
	return { approve: [synonym] }
}

export const constructUnapprovedSynonymPayload = (synonyms: Icd10Synonym[]) => {

	const all_approved: Icd10Synonym[] = synonyms.filter(synonym => synonym.action_chosen === 'approve');
	let icd_append: any[] = [];


	const patch_synonyms: Icd10Synonym[] = synonyms.filter(synonym =>
		synonym.action_chosen === 'approve' || synonym.action_chosen === 'disapprove'
	).map(synonym => {
		const status: string = synonym.action_chosen === 'approve' ? 'active' : synonym.action_chosen === 'disapprove' ? 'inactive' : '';
		delete synonym['action_chosen'];
		synonym.status = status;
		return synonym;
	});



	all_approved.forEach(synonym => {
		let synonyms: string[] = [];
		if (icd_append.some(item => item.full_code === synonym.full_code)) {
			synonyms = icd_append.filter(item => item.full_code === synonym.full_code)[0]['synonyms'];
			icd_append = icd_append.filter(item => item.full_code !== synonym.full_code);
		}

		const synonym_lowercase: string = synonym.title.toLowerCase();
		if (!synonyms.includes(synonym_lowercase)) synonyms.push(synonym_lowercase);

		icd_append.push({
			full_code: synonym.full_code,
			synonyms: synonyms
		});
	});

	console.log('PATCH SYNONYMS', patch_synonyms, 'ICD APPEND', icd_append);
	return { patch_synonyms: patch_synonyms, icd_append_synonyms: icd_append }

}


export const constructDeactivateSynonymPayload = (synonyms: Icd10Synonym[]) => {

	const for_deactivate: Icd10Synonym[] = synonyms.filter(synonym => synonym.action_chosen);
	let icd_discard: any[] = [];


	const patch_synonyms: Icd10Synonym[] = synonyms.filter(synonym =>
		synonym.action_chosen
	).map(synonym => {
		const status: string = 'inactive'
		delete synonym['action_chosen'];
		synonym.status = status;
		return synonym;
	});



	for_deactivate.forEach(synonym => {
		let synonyms: string[] = [];
		if (icd_discard.some(item => item.full_code === synonym.full_code)) {
			synonyms = icd_discard.filter(item => item.full_code === synonym.full_code)[0]['synonyms'];
			icd_discard = icd_discard.filter(item => item.full_code !== synonym.full_code);
		}

		const synonym_lowercase: string = synonym.title.toLowerCase();
		if (!synonyms.includes(synonym_lowercase)) synonyms.push(synonym_lowercase);

		icd_discard.push({
			full_code: synonym.full_code,
			synonyms: synonyms
		});
	});

	console.log('PATCH SYNONYMS', patch_synonyms, 'ICD DISCARD', icd_discard);
	return { patch_synonyms: patch_synonyms, icd_discard_synonyms: icd_discard }

}


