export const constructEmptyDoctorSpecialization = () => {
	const payload: DoctorSpecialization = {
		_id: '',
		code: '',
		description: '',
		effectivity_date: '',
		expiry_date: '',
		status: '',
	}
	return payload
}

export const constructDoctorSpecialization = (data: DoctorSpecialization) => {

	if (!data || !data._id || !data.code || !data.description || !data.status || !data.effectivity_date) return constructEmptyDoctorSpecialization();

	Object.keys(data).forEach(key => {
		if (!data[key]) data[key] = '';
	});

	return data;
}

export const constructPutDoctorSpecialization = (data: DoctorSpecialization) => {
	const payload: DoctorSpecialization = Object.assign({}, data)

	if (data && data._id && data.code && data.description && data.effectivity_date && data.status) {
		return payload;
	} else {
		return constructEmptyDoctorSpecialization();
	}
	
}

export const constructPostDoctorSpecialization = (data: any) => {
	let data_arr: DoctorSpecialization[] = [];

	data_arr.push(data)
	 
	const payload: DoctorSpecialization[] = data_arr.map(item => {
		if(item['expiry_date'] === '') {
			delete item['expiry_date'];
		}
		item['status'] = 'active'
		
		delete item['_id']
		return item;
	})
	return payload

}

export const constructEmptyRVS = () => {
	const payload: RVS = {
		_id: '',
		code: '',
		ruv: 0,
		body_part: '',
		procedure: '',
		description: '',
		effectivity_date: '',
		expiry_date: '',
		status: ''
	}
	return payload
}

export const constructRVS = (data: RVS) => {
	if(!data || !data._id || !data.code || !data.body_part || !data.procedure || !data.description || data.ruv === undefined || !data.status) 
	return constructEmptyRVS();

	Object.keys(data).forEach(key => {
		if(!data[key] || data[key] === 'N/A') data[key] = ''
	});

	return data;
}