export const getICDSynonyms = (filters?: any, query?: string, limit?: number, offset?: number, sorting?: any) => {

    let uri_obj: any = {};
    let uri_string: string = `icd10-synonyms/list`;
    let filter = {};
    if (filters && Array.isArray(filters) && filters.length > 0) {
        filter['where'] = {};
        filters.forEach(item => {
            if (item['columnName'] !== 'status')
                filter['where'][item['columnName']] = { like: item['value'], options: 'i' }
            else
                filter['where'][item['columnName']] = item['value']
        });
    }

    if (sorting && Array.isArray(sorting) && sorting.length > 0)
        filter['order'] = `${sorting[0]['columnName']} ${sorting[0]['direction']}`;

    if (offset) filter['skip'] = offset;

    if (limit) filter['limit'] = limit;

    if (query) uri_obj['search'] = query;

    if (Object.entries(filter).length > 0) uri_obj['filter'] = JSON.stringify(filter);

    let i = 0;
    for (let key of Object.keys(uri_obj)) {
        if (!i) uri_string += '?';
        else uri_string += '&';
        uri_string += `${key}=${uri_obj[key]}`;
        i += 1;
    }

    let option: requestOption = {
        method: 'GET'
    }

    if (fetch.getAccessToken()) {
        return fetch.call(option, uri_string, true);
    }
    return fetch.fetchAccessToken().then(() => fetch.call(option, uri_string, true))
}


export const getActiveSynonyms = (
    filters?: any,
    query?: string,
    limit?: number,
    offset?: number,
    sorting?: any
) => {
    let filter: any = {
        limit: limit,
        skip: offset,
        where: filters
    }

    let search_str = '';
    if (query) search_str = '&search=' + query;


    if (sorting) {
        filter['order'] = sorting;
    }

    let option: requestOption = {
        method: 'GET'
    }

    return fetch.fetchAccessToken().then(() => fetch.call(option, `icd10-synonyms/search?filter=${JSON.stringify(filter)}${search_str}`, true))
}


