import Fetch, { requestOption } from '../index'

const fetch = new Fetch();
fetch.getAccessToken();

export const getCPT = (limit: number, skip: number) => {

    let filters = {
        limit: limit,
        skip: skip,
      };
    let option: requestOption = {
        method: 'GET'
    }
    if(fetch.getAccessToken()){
        return fetch.call(option, `cpts/search?filter=${JSON.stringify(filters)}`, true)
    }
    return fetch.fetchAccessToken().then(()=> fetch.call(option, `cpts/search?filter=${JSON.stringify(filters)}`, true))
}

export const searchCPT = (limit: number, offset: number, filters: any, query: string, sorting?: any, key?: string,) => {


    if(!limit) limit = 10;

    let params: any = {};
    let filter: any = {
        limit: limit,
        offset: offset,
    }
    // let filter_key_str = '';
    if (key !== undefined && key !== '' && key.length > 0) {
        // console.log('KEY', key)
        // filter_key_str = '&filter[where][category][like]='+key
        filter['where'] = {category: key}
        // filter['where']['category'] = key;
    }
    if (sorting && sorting.length !== 0) {
        filter['order'] = sorting[0]['columnName'] +' '+sorting[0]['direction'];
    }
    let filter_str = '';
    if (filters) {
        
      filters.forEach(item => {
        params[item['columnName']]=item['value'];
        filter_str =
          filter_str + '&' + item['columnName'] + '=' + item['value'];
      });
    }
    if(query){
        params['search']=query;
    }
    params['filter'] = JSON.stringify(filter);

    let append = new URLSearchParams(params).toString();
    console.log(append)
    let CPT_URL =
      `cpts/search?${append}`

    let option: requestOption = {
        method: 'GET'
    }
 
    if(fetch.getAccessToken()){
        return fetch.call(option, CPT_URL, true)
    }
    return fetch.fetchAccessToken().then(()=> fetch.call(option, CPT_URL, true))
}

export const addCPTCodes = (payload: any) => {

    let option: requestOption = {
        method: 'POST',
        body: payload
    }

    if(fetch.getAccessToken()){
        return fetch.call(option, `cpts/multiple`, true)
    }
    return fetch.fetchAccessToken().then(()=> fetch.call(option, `cpts/multiple`, true))
}

export const editCPT = (payload: any, id: string | undefined) => {

    let option: requestOption = {
        method: 'PUT',
        body: payload
    }

    if(fetch.getAccessToken()){
        return fetch.call(option, `cpts/edit/${id}`, true)
    }
    return fetch.fetchAccessToken().then(()=> fetch.call(option, `cpts/edit/${id}`, true))
}

export const deleteCPT = (id: string) => {

    let option: requestOption = {
        method: 'DELETE'
    }

    if(fetch.getAccessToken()){
        return fetch.call(option, `cpts/${id}`, true)
    }
    return fetch.fetchAccessToken().then(()=> fetch.call(option, `cpts/${id}`, true))
}