import React from 'react';
import { TableComponent } from 'Components/UI/TableComponent/CodeListTable';
import {
  TextField,
  InputAdornment,
  Grid,
  List,
  ListItemText,
} from '@material-ui/core';
import { Search } from '@material-ui/icons/';
import { Loader } from '../Loader';
import Link from '@material-ui/core/Link';
import { GridHorizontalDivider } from 'Components/UI';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { SideModalBlock } from 'Components/UI';
import { SideModalBody } from 'Components/UI';
import { MsgModal } from 'Components/UI';
import { SIDE_MODAL_BLOCK_FIELDS } from 'Components/UI/SideModalBlock/FieldConstants';
import moment from 'moment';
import { ConfirmationModal } from '../ConfirmationModal'
import {
  getCPT,
  searchCPT,
  addCPTCodes,
  editCPT,
  deleteCPT
} from 'Pages/API/Calls/CPT'
import { cloneDeep } from 'lodash'
import { Utils } from '@hims/core'
import {
  constructCpt,
  constructEmptyCpt,
  constructPutCpt,
  constructPostCpt,
  reconstructCpt
} from '../ConstructPayloads'
import { Cpt } from '../../LookupModels';
const helper = new Utils.IndexedDBHelper();

interface IState {
  confirm_modal_title: string;
  confirm_modal_message: string;
  confirm_modal_open: boolean;
  action_after_confirm: () => void;
  confirm_modal_button: string;
  invalid_fields: string[];
  loading_state: boolean;
  block_title?: string;
  show_side_modal_block?: string;
  side_modal_block_mode: 'view' | 'edit' | 'add' | '';
  code_info?: Cpt;
  code_response: any;
  code_data_all: any;
  code_data: any;
  global_filter: string;
  default_filters: any;
  default_sorting: any;
  default_clicked: string;
  isModalOpen: boolean;
  modalTitle: string;
  modalMessage: any;
  totalCount: number;
  typing: boolean;
  typingTimeout: any;
  page: number;
  limit: number;
  isPageSizeModal: boolean;
  highlightedCodeFromList: number;
  no_data_message: 'Searching for CPT codes' | 'No available data' | '';
  cpt_categories: string[];
  cpt_category_range: string[];
  disableSaveButton: boolean;
  clickSaveButton: boolean;
  category_dropdown_choices: any[];
}

interface IProps {
  classes: any


  cptCategories: string[];
  cptCategoryRanges: string[];
  cptCategoryDropdown: any;


  setCPTCategories: (categories: string[]) => void;
  setCPTCategoryRanges: (ranges: string[]) => void;
  setCPTCategoryDropdown: (dropdown: any) => void;
}


export class CPTSection extends React.Component<IProps, IState> {
  private requiredFields: any;
  private excludeFields: any;

  constructor(props) {
    super(props);

    this.requiredFields = SIDE_MODAL_BLOCK_FIELDS.CPT.editOrAdd.requiredFields;
    this.excludeFields = SIDE_MODAL_BLOCK_FIELDS.CPT.editOrAdd.excludeFields

    const table: any = {
      formattedColumns: {},
      columnExtensions: [
        {
          columnName: 'code',
          width: 150,
          wordWrapEnabled: true,
        },
        {
          columnName: 'description',
          width: 500,
          wordWrapEnabled: true,
        },
      ],
      columns: [
        {
          name: 'code',
          title: 'CPT Code',
        },
        {
          name: 'description',
          title: 'Description',
        },
      ],
      rows: [],
    };

    this.state = {
      category_dropdown_choices: props.cptCategoryDropdown,
      cpt_categories: props.cptCategories,
      cpt_category_range: props.cptCategoryRanges,
      no_data_message: '',
      confirm_modal_title: '',
      confirm_modal_message: '',
      confirm_modal_open: false,
      action_after_confirm: () => { },
      confirm_modal_button: 'Confirm',
      invalid_fields: [],
      highlightedCodeFromList: -1,
      loading_state: true,
      code_response: [],
      code_info: constructEmptyCpt(),
      show_side_modal_block: 'none',
      side_modal_block_mode: '',
      code_data: table,
      code_data_all: table,
      block_title: '',
      global_filter: '',
      default_filters: [],
      default_sorting: [{
        columnName: 'code',
        direction: 'asc'
      }],
      default_clicked: '',
      isModalOpen: false,
      isPageSizeModal: false,
      modalTitle: '',
      modalMessage: '',
      totalCount: 0,
      typing: false,
      typingTimeout: 0,
      page: 0,
      limit: 10,
      disableSaveButton: false,
      clickSaveButton: false,
    };
  }


  componentDidMount() {
    if (!this.state.category_dropdown_choices || !this.state.cpt_categories || !this.state.cpt_category_range) {
      this.fetchCPTCategories()
        .then(() => this.initialData())
        .catch(() => this.initialData())
    } else {
      this.initialData()
    }

    document.addEventListener('keydown', this.escFunction, false);

  }

  escFunction = (event) => {
    if (event.keyCode === 27) {
      this.handleCloseSideModalBlock()
    }
  }


  initialData = () => {
    this.setState({ no_data_message: 'Searching for CPT codes' })
    this.searchCode(
      10,
      0,
      this.state.default_filters,
      this.state.global_filter,
      this.state.default_sorting,
      this.state.default_clicked)
  };


















  /***********************  
       CRUD FUNCTIONS 
  ***********************/

  addCPTIndexDB = (data: any[]) => {
    helper.openDb('himsDb').then(res => {
      let db = res.result
      try {
        helper.saveToStore(db, 'cpt_list', data)
          .then(res => console.log('RES', res))
          .catch(e => console.log(e))
      } catch (e) {
        console.log(e)
      } finally { }
    })
  }

  patchCPTIndexDB = (data: any) => {
    helper.openDb('himsDb').then(res => {
      let db = res.result
      try {
        helper.updateEntryByKey(db, 'cpt_list', data['_id'], data)
          .then(res => console.log('RES', res))
          .catch(e => console.log(e))
      } catch (e) {
        console.log(e)
      } finally { }
    })
  }

  fetchCPTCategories = async () => {
    this.setState({ loading_state: true })
    await Utils.StorageService('cpt_list')
      .then(cpts => {
        if (Array.isArray(cpts) && cpts.length > 0) {
          let category_arr: string[] = []
          let category_ranges: string[] = []
          let category_dropdown_choices_arr: any[] = [{ name: '', value: '' }]
          let all: any[] = [];
          cpts.filter(item => {
            if (!category_arr.includes(item.category)) {
              category_arr.push(item.category)
              category_ranges.push(item.code)
              category_dropdown_choices_arr.push({ name: item.category, value: item.category })
              all.push({category: item.category, code: item.code, choice: { name: item.category, value: item.category }})
              return true;
            }
            return false;
          })
          category_arr = [];
          category_ranges = [];
          category_dropdown_choices_arr = [{ name: '', value: '' }];
          all = all.sort((a,b) => (a.category > b.category) ? 1 : -1);
          all.forEach(item => {
            category_arr.push(item.category);
            category_ranges.push(item.code);
            category_dropdown_choices_arr.push({ name: item.category, value: item.category });
          });
          this.props.setCPTCategories(category_arr)
          this.props.setCPTCategoryDropdown(category_dropdown_choices_arr)
          this.props.setCPTCategoryRanges(cloneDeep(category_ranges))
          this.setState({
            cpt_categories: category_arr,
            cpt_category_range: category_ranges,
            category_dropdown_choices: category_dropdown_choices_arr
          }, () => this.setState({ loading_state: false }))
        }
      })
      .catch(err => console.log(err));
  }

  patchCode = (data: any) => {

    const payload: Cpt = constructPutCpt(data)

    const code: string = payload.code;

    this.setState({
      loading_state: true,
      no_data_message: 'Searching for CPT codes'
    })
    editCPT(JSON.stringify(payload), payload._id).then(response => {
      this.setState({
        no_data_message: 'No available data',
        loading_state: false
      })
      if (response && response.error === undefined && response.message) {
        this.changePageUpdate(this.state.limit, this.state.limit * this.state.page)
        this.patchCPTIndexDB(data)
        this.setState({
          disableSaveButton: true,
          isModalOpen: true,
          modalTitle: 'Success',
          modalMessage: `You have ${response.message} code!`
        });
      } else if (response && response.error && response.error.statusCode === 401) {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Unauthorized',
          modalMessage: 'You are not authorized to edit CPT codes.'
        });
      } else if (response && response.error && response.error.statusCode === 400 && response.error.message) {
        this.setState({
          isModalOpen: true,
          modalTitle: 'CPT Code Already Exists',
          modalMessage: (<span><strong>[{code}]</strong> cannot be added. This already exists on the list.</span>),
        });
      } else {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Failed',
          modalMessage: 'Something went wrong while saving!'
        });
      }
    }).catch(error => {
      if (error == 'Error: This code already exists.') {
        this.setState({
          isModalOpen: true,
          modalTitle: 'CPT Code Already Exists',
          modalMessage: (<span><strong>[{code}]</strong> cannot be added. This already exists on the list.</span>),
          loading_state: false
        });
      } else {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Failed',
          modalMessage: 'Something went wrong while saving!',
          loading_state: false
        });
      }
    });
  }

  addCodes = (data: any) => {

    this.setState({
      loading_state: true,
      no_data_message: 'Searching for CPT codes'
    })

    const payload: Cpt[] = constructPostCpt(data)
    let code: string = '';
    if (payload && payload.length > 0) code = payload[0].code;
    addCPTCodes(JSON.stringify(payload)).then(response => {
      this.setState({
        loading_state: false,
        no_data_message: 'No available data'
      })
      if (response && response.error === undefined && response.message && response.result) {
        this.changePageUpdate(this.state.limit, this.state.limit * this.state.page)
        this.addCPTIndexDB(response.result)
        this.setState({
          disableSaveButton: true,
          isModalOpen: true,
          modalTitle: 'Success',
          modalMessage: `You have successfully added ${response.result.length} CPT code/s!`
        });
      } else if (response && response.error && response.error.statusCode === 401) {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Unauthorized',
          modalMessage: 'You are not authorized to add CPT codes.'
        });
      } else if (response && response.error && response.error.statusCode === 400 && response.error.message) {
        this.setState({
          isModalOpen: true,
          modalTitle: 'CPT Code Already Exists',
          modalMessage: (<span><strong>[{code}]</strong> cannot be added. This already exists on the list.</span>),
        });
      } else {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Failed',
          modalMessage: 'Something went wrong while adding!'
        });
      }
    }).catch(error => {
      if (error == 'Error: This code already exists.') {
        this.setState({
          isModalOpen: true,
          modalTitle: 'CPT Code Already Exists',
          modalMessage: (<span><strong>[{code}]</strong> cannot be added. This already exists on the list.</span>),
          loading_state: false
        });
      } else {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Failed',
          modalMessage: 'Something went wrong while saving!',
          loading_state: false
        });
      }
    });
  }

  searchCode = (
    limit: number, skip: number, filters: any, filters_global: any, sorting: any, clicked: any) => {

    searchCPT(limit, skip, filters, filters_global, sorting, clicked).then(response => {
      this.setState({ no_data_message: 'No available data' })
      if (response && response.error === undefined) {
        const table = this.generateDataForTable(
          response.result ? response.result : [],
        );
        this.setState({
          code_response: response,
          code_data_all: table,
          code_data: table,
          loading_state: false,
          totalCount:
            response.total && response.total.count
              ? response.total.count
              : 0,
        });
      } else if (response && response.error && response.error.statusCode === 401) {
        const table = this.generateDataForTable([]);
        this.setState({
          code_response: [],
          code_data_all: table,
          loading_state: false,
          isModalOpen: true,
          modalTitle: 'Unauthorized',
          modalMessage: 'You are not authorized to view CPT codes.',
        });
      } else {
        const table = this.generateDataForTable([]);
        this.setState({
          code_response: [],
          code_data_all: table,
          loading_state: false,
          isModalOpen: true,
          modalTitle: 'Loading failed',
          modalMessage: 'An error occured while loading the list of codes.',
        });
      }
    }).catch(() => {
      const table = this.generateDataForTable([]);
      this.setState({
        code_response: [],
        code_data_all: table,
        loading_state: false,
        isModalOpen: true,
        modalTitle: 'Loading failed',
        modalMessage: 'An error occured while loading the list of codes.',
      });
    });
  }


  deleteCPT = (id: string) => {
    if (!id) {
      return
    }
    this.setState({ loading_state: true })
    deleteCPT(id).then(response => {
      this.setState({
        loading_state: false
      })
      this.changePageUpdate(this.state.limit, this.state.limit * this.state.page)
      if (response && response.error) {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Failed',
          modalMessage: 'Something went wrong while deleting the CPT Code!'
        });
      }
      else {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Success',
          modalMessage: 'You have successfully deleted a CPT code!'
        });
      }
    }).catch(error => {
      this.setState({
        isModalOpen: true,
        modalTitle: 'Failed',
        modalMessage: 'Something went wrong while deleting the ICD10 Code!'
      });
      this.setState({
        loading_state: false
      })
      console.log('client error: ', error);
    });
  }













  /***********************  
  TABLE AND PAGE FUNCTIONS 
  ***********************/

  generateDataForTable(response: any) {
    const table: any = {
      formattedColumns: {},
      columnExtensions: [
        {
          columnName: 'code',
          width: 150,
          wordWrapEnabled: true,
        },
        {
          columnName: 'description',
          width: 500,
          wordWrapEnabled: true,
        },
      ],
      columns: [
        {
          name: 'code',
          title: 'CPT Code',
        },
        {
          name: 'description',
          title: 'Description',
        },
      ],
      rows: [],
    };
    for (const cnt in response) {
      const item = response[cnt];
      const temp_row: any = {};
      temp_row['code'] = item['code'];
      temp_row['description'] = item['description'];
      table['rows'].push(temp_row);
    }

    return table;
  }

  changePageUpdate = (limit: number, skip: number) => {
    this.handleCloseSideModalBlock();
    this.setState({ no_data_message: 'Searching for CPT codes' })
    this.setState({
      loading_state: true,
    });
    if (
      this.state.default_filters.length !== 0 ||
      this.state.global_filter !== '' ||
      this.state.default_sorting.length !== 0 ||
      this.state.default_clicked !== ''
    ) {
      this.searchCode(
        limit,
        skip,
        this.state.default_filters,
        this.state.global_filter,
        this.state.default_sorting,
        this.state.default_clicked,
      )
    } else {
      this.setState({ no_data_message: 'Searching for CPT codes' })
      getCPT(limit, skip).then(response => {
        this.setState({ no_data_message: 'No available data' })
        if (response && response.error === undefined) {
          const table = this.generateDataForTable(
            response.result ? response.result : [],
          );
          this.setState({
            code_response: response,
            code_data_all: table,
            code_data: table,
            loading_state: false,
            totalCount:
              response.total && response.total.count
                ? response.total.count
                : 0,
          });
        } else if (response && response.error && response.error.statusCode === 401) {
          const table = this.generateDataForTable([]);
          this.setState({
            code_response: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Unauthorized',
            modalMessage: 'You are not authorized to view CPT codes.',
          });
        } else {
          const table = this.generateDataForTable([]);
          this.setState({
            code_response: [],
            code_data_all: table,
            loading_state: false,
            isModalOpen: true,
            modalTitle: 'Loading failed',
            modalMessage: 'An error occured while loading the list of codes.',
          });
        }
      });
    }
  };

  handleChangeSorting = (sorting: any) => {
    this.setState({ no_data_message: 'Searching for CPT codes' })
    this.setState({
      default_sorting: sorting,
      loading_state: true,
    });
    if (sorting && sorting.length > 0) {

      this.searchCode(
        this.state.limit,
        this.state.page * this.state.limit,
        this.state.default_filters,
        this.state.global_filter,
        sorting,
        this.state.default_clicked,
      )
    }
  };

  handleFilterByColumn = (filter: any) => {
    const self = this;

    if (self.state.typingTimeout) {
      clearTimeout(self.state.typingTimeout);
    }

    self.setState({
      default_filters: filter,
      typing: false,
      page: 0,
      typingTimeout: setTimeout(function () {
        self.setState({
          no_data_message: 'Searching for CPT codes',
          loading_state: true,
        });
        if (self.state.default_filters !== 0) {
          self.searchCode(
            self.state.limit,
            0,
            filter,
            self.state.global_filter,
            self.state.default_sorting,
            self.state.default_clicked,
          )
        } else {
          self.initialData();
        }
      }, 2000),
    });

  };

  handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target;

    const self = this;

    if (self.state.typingTimeout) {
      clearTimeout(self.state.typingTimeout);
    }
    self.setState({
      global_filter: value,
      typing: true,
      page: 0,
      typingTimeout: setTimeout(function () {
        self.setState({
          typing: false,
          no_data_message: 'Searching for CPT codes',
          loading_state: true,
          limit: 10,
        });
        if (self.state.global_filter !== '') {
          self.searchCode(
            self.state.limit,
            0,
            self.state.default_filters,
            self.state.global_filter,
            self.state.default_sorting,
            self.state.default_clicked,
          )
        } else {
          self.initialData();
        }
      }, 2000),
    });
  };

  handleClickRow = (row: any) => {
    const { code_response } = this.state;

    if (!this.state.code_response || !this.state.code_response.result)
      return;

    const data: Cpt = constructCpt(code_response.result[row])
    this.checkFields(data, true)
    this.setState({
      code_info: data,
    }, () => this.handleChangeSideModalBlockMode('view'));
  };


  handlePageChange = (currentPage: number, skip: number) => {
    this.setState({
      page: currentPage,
    });

    this.changePageUpdate(this.state.limit, skip);
  };

  handlePageSizeChange = value => {
    let pageLimit = 100000;

    if (this.state.totalCount >= pageLimit) {
      this.setState({
        isPageSizeModal: true,
      });
    } else {
      this.setState({
        limit: value,
      });

      this.changePageUpdate(value, value * this.state.page);
    }
  };

  handleClickItemFromList = (key: string) => {
    this.setState({ default_clicked: key });
    this.handleCloseSideModalBlock()
    this.setState({
      no_data_message: 'Searching for CPT codes',
      page: 0,
      loading_state: true,
    });
    if (key !== undefined) {
      this.searchCode(
        this.state.limit,
        0,
        this.state.default_filters,
        this.state.global_filter,
        this.state.default_sorting,
        key,
      )
    }
  };



































  /*****************************  
  ACTIONS / SIDE MODAL FUNCTIONS 
  ******************************/

  handleCloseSideModalBlock = () => {
    this.setState({
      show_side_modal_block: 'none',
      side_modal_block_mode: '',
      code_info: constructEmptyCpt(),
    });
  };

  handleChangeSideModalBlockMode = (value: 'view' | 'edit' | 'add' | '') => {
    let title = '';
    if (value && value === 'edit' && this.state.block_title) {
      title = `Edit ${this.state.block_title}`;
      const reform_data: Cpt = reconstructCpt(cloneDeep(this.state.code_info))
      this.setState({ code_info: reform_data })
    } else if (value && value === 'view' && this.state.code_info) {
      title = this.state.code_info['code'] ? this.state.code_info['code'] : '';
    } else if (value && value === 'add') {
      title = `Add CPT Code`;
    }
    this.setState({
      side_modal_block_mode: value,
      block_title: title,
    }, () => this.handleOpenBlock());
  };

  handleOpenBlock = () => {
    this.setState({
      show_side_modal_block: 'block',
    }, () => this.passDataToSideModalBlockBody());
  };

  handleInvalidFieldsPrompt = () => {
    this.setState({
      isModalOpen: true,
      modalTitle: 'Incomplete/Invalid Fields',
      modalMessage: 'Please complete the form before proceeding.',
    })
  }


  passDataToSideModalBlockBody = () => {

    if (this.state.code_info && !this.state.code_info.gender_limited) {
      this.state.code_info.gender = ''
      this.setState({
        code_info: this.state.code_info
      });
    }
  };


  checkFields = (formFields?: any, disableButton?: boolean) => {
    this.setState({ loading_state: true })

    if (formFields) {
      let invalid_fields = this.getInvalidFields(formFields, disableButton);
      if (!invalid_fields || invalid_fields.length === 0) {
        if (this.state.disableSaveButton || !this.state.clickSaveButton) {
          return
        }
        if (this.state.side_modal_block_mode === 'edit') {
          let message: any = '';
          let title: string = '';
          let button: string = '';
          if (formFields && formFields['expiry_date'] && moment(formFields['expiry_date']).isValid() && moment(new Date()).isSameOrAfter(moment(formFields['expiry_date']).startOf('day'))) {
            title = 'Deactivate CPT Code'
            message = <span style={{ display: 'inline-block', width: '350px' }}>Would you like to continue deactivating <b>CPT Code [{formFields['code']}]</b>?</span>
            button = 'Deactivate';
          } else {
            button = 'Confirm';
            title = 'Edit CPT Code'
            message = <span style={{ display: 'inline-block', width: '350px' }}>Would you like to continue editing <b>CPT Code [{formFields['code']}]</b>?</span>
          }
          this.setState({
            clickSaveButton: false,
            confirm_modal_button: button,
            confirm_modal_message: message,
            confirm_modal_open: true,
            confirm_modal_title: title,
            action_after_confirm: () => this.patchCode(formFields)
          })
        } else if (this.state.side_modal_block_mode === 'add') {
          this.setState({
            confirm_modal_button: 'Confirm',
            clickSaveButton: false,
            confirm_modal_message: 'Are you sure you want to add this CPT code?',
            confirm_modal_open: true,
            confirm_modal_title: 'Confirm add CPT code',
            action_after_confirm: () => this.addCodes(formFields)
          })
        }
      } else {
        this.handleInvalidFieldsPrompt()
      }
    }
  }

  getInvalidFields = (formFields: any, disableButton?: boolean) => {
    let invalid_fields_submit: any[] = [];
    let invalid_fields_editing: any[] = [];
    let fields_excluded: string[] = []
    if (this.excludeFields !== undefined) {
      fields_excluded = this.excludeFields
    }
    // const fields_excluded: string[] = Object.keys(exclude_fields)
    const form_keys: string[] = Object.keys(formFields)

    form_keys.forEach(field => {
      if (this.requiredFields !== undefined &&
        this.requiredFields.includes(field) &&
        !fields_excluded.includes(field) &&
        !formFields[field]) {
        if (!disableButton) {
          invalid_fields_submit.push(field)
        } else {
          invalid_fields_editing.push(field)
        }

      }
    });

    const eff_date = moment(formFields['effectivity_date'])
    const exp_date = moment(formFields['expiry_date'])
    if (formFields &&
      formFields['effectivity_date'] &&
      formFields['expiry_date'] &&
      eff_date.isValid() &&
      exp_date.isValid() &&
      eff_date.startOf('day').diff(exp_date.startOf('day')) > 0) {
      if (disableButton) {
        invalid_fields_editing.push('effectivity_date')
      } else {
        invalid_fields_submit.push('effectivity_date')
      }
    }

    if (formFields && formFields['gender_limited'] && !formFields['gender']) {
      if (!disableButton) {
        invalid_fields_submit.push('gender')
      } else {
        invalid_fields_editing.push('gender')
      }
    }
    if (!invalid_fields_editing || invalid_fields_editing.length === 0) {
      this.setState({ disableSaveButton: false })
    } else if (!this.state.disableSaveButton) {
      this.setState({ disableSaveButton: true })
    }
    this.setState({ loading_state: false, invalid_fields: invalid_fields_submit })
    return invalid_fields_submit
  }





































  renderSideModalBlock(): JSX.Element {

    return (
      <SideModalBlock
        blockPosition={'fixed'}
        title={this.state.block_title}
        editIconDescription={'Edit Code'}
        showBlock={this.state.show_side_modal_block}
        handleClose={this.handleCloseSideModalBlock}
        sideModalBlockMode={this.state.side_modal_block_mode}
        changeMode={this.handleChangeSideModalBlockMode}
      >
        {this.state.side_modal_block_mode ? (
          <SideModalBody
            showRequiredFields
            formFields={this.state.code_info}
            invalidFields={this.state.invalid_fields}
            handleClose={this.handleCloseSideModalBlock}
            disableSaveButton={this.state.disableSaveButton}
            dynamicDropdownChoices={this.state.category_dropdown_choices}
            refreshData={(data: any) => {
              this.setState({ code_info: data },
                () => this.checkFields(data, true))
            }}
            delete={(data: any) => {
              if (this.state.side_modal_block_mode) {
                if (this.state.side_modal_block_mode === 'edit') {
                  this.setState({
                    confirm_modal_message: 'Are you sure you want to delete this CPT code?',
                    confirm_modal_open: true,
                    confirm_modal_title: 'Confirm delete CPT code',
                    action_after_confirm: () => this.deleteCPT(data['_id'])
                  })
                }
              }
            }}
            handleSave={(formFields?: any) => {
              if (this.state.side_modal_block_mode) {
                this.setState({ clickSaveButton: true }, () => this.checkFields(formFields))
              }
            }}
            formName={'CPT'}
            mode={this.state.side_modal_block_mode ? this.state.side_modal_block_mode : ''}
            checkBoxWithDropDownFieldsInitState={this.state.code_info && this.state.code_info['gender_limited'] ? true : false}
          />
        ) : null}
      </SideModalBlock>
    )
  }

  renderCodeInfo(): JSX.Element {
    const { classes } = this.props;

    return (
      <React.Fragment>
        <li key={'version'} className={classes.codeHeader}>VERSION</li>
        <li key={'year-of-release-value'} className={classes.codeHeaderValue}>2020</li>
        <ListItemText key={'effectivity-date'}>
          <div className={classes.codeHeader}>EFFECTIVITY DATE</div>
          <div className={classes.codeHeaderValue}>Jan 1, 2020</div>
        </ListItemText>
      </React.Fragment>
    )
  }

  renderCodeList(key: string): JSX.Element {
    const { classes } = this.props;
    const ranges = this.state.cpt_category_range
    const categories = this.state.cpt_categories

    let display: string = ''
    if (ranges.length > 0 && categories.length > 0) {
      const range = ranges[categories.indexOf(key)]
      if (range) {
        display = `${range.slice(0, 4)}0000 - ${range.slice(0, 4)}9999 :`
      }
    } else {
      return (
        <>
        </>
      )
    }

    return (
      <ListItemText
        key={key}
        classes={{
          root: classes.codeListItemsRoot,
          primary:
            this.state.highlightedCodeFromList ===
              categories.indexOf(key)
              ? classes.codeListItemsSelected
              : classes.codeListItems,
        }}
        onClick={() => {
          if (categories.indexOf(key) !== this.state.highlightedCodeFromList) {
            this.setState({
              highlightedCodeFromList: categories.indexOf(key),
            });
            this.handleClickItemFromList(key);
          } else {
            this.setState({
              highlightedCodeFromList: -1,
            });
            this.handleClickItemFromList('');
          }
        }}
      >
        <br />{display}<br />
        {key}
      </ListItemText>
    )
  }


  render(): JSX.Element {
    const { classes } = this.props;
    const categories = this.state.cpt_categories

    return (
      <div>
        {this.state.loading_state ? <Loader /> : null}


        <Grid container>
          <Grid container item xs={12} md={3}>
            <Grid container className={classes.borderedGrid}>
              <List>
                {this.renderCodeInfo()}
                {categories ? categories.map(key => this.renderCodeList(key)) : null}
              </List>
            </Grid>
          </Grid>







          <Grid container item xs={12} md={9} style={{ paddingLeft: '37px' }}>
            <Grid container>
              <Grid item xs={12} md={9}>
                <TextField
                  style={{ width: '100%', backgroundColor: 'white' }}
                  id="code_search"
                  placeholder="Search a code or description"
                  label=""
                  margin="none"
                  fullWidth
                  // className={classes.searchField}
                  variant="outlined"
                  value={this.state.global_filter}
                  inputProps={{ 'aria-label': 'bare' }}
                  onChange={this.handleSearch}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <Search style={{ color: 'rgba(54, 54, 54, 0.4)' }} />
                      </InputAdornment>
                    ),
                  }}
                  onKeyUp={(event) => {
                    let code = event.keyCode || event.which
                    if (code == 13 && this.state.typing && this.state.global_filter) {
                      if (this.state.typingTimeout) {
                        clearTimeout(this.state.typingTimeout);
                      }
                      this.setState({
                        loading_state: true,
                        typing: false,
                        limit: 10,
                        no_data_message: `Searching for CPT codes`
                      });
                      this.searchCode(
                        this.state.limit,
                        0,
                        this.state.default_filters,
                        this.state.global_filter,
                        this.state.default_sorting,
                        this.state.default_clicked,
                      )
                    }
                  }}
                />
              </Grid>




              <Grid item xs={8} md={3} style={{ paddingRight: '15px', marginTop: 15, textAlign: 'center' }}>
                <FontAwesomeIcon icon={faPlus} className={classes.plusIcon} />
                <Link className={classes.addACode}
                  onClick={() => {
                    this.setState({
                      block_title: 'Add CPT Code',
                      code_info: constructEmptyCpt(),
                      disableSaveButton: true
                    }, () => this.handleChangeSideModalBlockMode('add'));
                  }}>Add a Code</Link>
              </Grid>





              <GridHorizontalDivider height="30px" />
              <Grid style={{ minHeight: 690, overflow: 'auto' }}>
                <TableComponent
                  id="tab-all"
                  totalCount={this.state.totalCount}
                  update={this.changePageUpdate}
                  updateSorting={this.handleChangeSorting}
                  rows={
                    Object.keys(this.state.code_data).length > 0
                      ? this.state.code_data['rows']
                      : []
                  }
                  columns={
                    Object.keys(this.state.code_data).length > 0
                      ? this.state.code_data['columns']
                      : []
                  }
                  message={
                    this.state.global_filter
                      ? 'No available data.'
                      : this.state.no_data_message
                  }
                  onClickRow={this.handleClickRow}
                  disableSelect
                  disableSearch
                  formattedColumns={
                    Object.keys(this.state.code_data).length > 0
                      ? this.state.code_data['formattedColumns']
                      : {}
                  }
                  columnExtensions={
                    Object.keys(this.state.code_data).length > 0
                      ? this.state.code_data['columnExtensions']
                      : []
                  }
                  onFilterChange={this.handleFilterByColumn}
                  onSortingChange={this.handleChangeSorting}
                  onPageChange={this.handlePageChange}
                  ongPageSizeChange={this.handlePageSizeChange}
                  defaultFilter={this.state.default_filters}
                  currentPage={this.state.page}
                  limit={this.state.limit}
                  pageSizes={[10, 25, 50, 100]}
                />
              </Grid>
            </Grid>
          </Grid>
        </Grid>



        {this.renderSideModalBlock()}

        <MsgModal
          onClose={() => this.setState({ isModalOpen: false })}
          title={this.state.modalTitle}
          isModalOpen={this.state.isModalOpen}
          message={this.state.modalMessage}
        />
        <ConfirmationModal
          onClose={() => this.setState({ confirm_modal_open: false })}
          title={this.state.confirm_modal_title}
          isModalOpen={this.state.confirm_modal_open}
          submitType={this.state.confirm_modal_button}
          onSubmit={() => {
            if (this.state.action_after_confirm) {
              this.state.action_after_confirm()
            }
          }}
          message={this.state.confirm_modal_message} />
      </div>
    );
  }
}
