export const LookupPage = () => {
  const [activeHeader, setActiveHeader] = useState('Codes');
  const [activeClass, setActiveClass] = useState(0);
  const [activeItem, setActiveItem] = useState('ICD-10');



  const [icdCategories, setICDCategories] = useState<string[]>([]);
  const [icdCategoryDropdown, setICDCategoryDropdown] = useState<any>(undefined);

  const [icdSynonymAction, setICDSynonymAction] = useState<any[]>([]);

  const [cptCategories, setCPTCategories] = useState<string[]>([]);
  const [cptCategoryRanges, setCPTCategoryRanges] = useState<string[]>([]);
  const [cptCategoryDropdown, setCPTCategoryDropdown] = useState<any>(undefined);

  const classes = useStyles();
  const lookupClasses = useStyles2();

  const activeSideBarItem = (item) => setActiveItem(item);
  

  const handleMenuClick = (item, index) => {
    setActiveHeader(item);
    setActiveClass(index)
  }

  return (
    <div className={lookupClasses.main}>
      <Grid
        container
        justify="space-between"
        alignItems="center"
        spacing={0}
        className={lookupClasses.gridContainer}
      >
        <Grid item>
          <Typography className={lookupClasses.headerTitle}>Lookup</Typography>
        </Grid>
      </Grid>
      <GridHorizontalDivider height="15px" />








      {/* HEADER */}

      <Grid item xs={6}>
        <ul style={{ padding: '0', margin: '0' }}>
          {
            page_tab_titles.map((item, i) => (
              <li key={item} className={lookupClasses.menuList}>
                <Link className={classNames(lookupClasses.menuLink, activeClass === i ? lookupClasses.LinkActive : null)} onClick={() => handleMenuClick(item, i)}>{item}</Link>
              </li>
            ))
          }
        </ul>
      </Grid>











      <Grid container justify="space-between" spacing={0}>
        <GridHorizontalDivider height="30px" />


        {/* SIDE MENU */}
        <Grid item xs={2} className={lookupClasses.sideBarContainer}>
          <SideMenu
            items={sideBarItems[activeHeader]}
            activeItems={activeSideBarItem}
            reset={activeHeader}
          ></SideMenu>
        </Grid>


        {/* SECTIONS */}
        {activeItem === 'ICD-10' && activeHeader === 'Codes' ? (
          <Grid item xs={12} md={10}>
            <ICD10Section
              classes={classes}
              icdCategories={icdCategories}
              setICDCategories={setICDCategories}
              icdCategoryDropdown={icdCategoryDropdown}
              setICDCategoryDropdown={setICDCategoryDropdown}
            />
          </Grid>

        ) : activeItem === 'CPT' && activeHeader === 'Codes' ? (
          <Grid item xs={12} md={10}>
            <CPTSection
              classes={classes}
              cptCategories={cptCategories}
              setCPTCategories={setCPTCategories}
              cptCategoryRanges={cptCategoryRanges}
              setCPTCategoryRanges={setCPTCategoryRanges}
              cptCategoryDropdown={cptCategoryDropdown}
              setCPTCategoryDropdown={setCPTCategoryDropdown}
            />
          </Grid>
        ) : activeItem === 'ICD10 Synonyms' && activeHeader === 'Codes' ? (
          <Grid item xs={12} md={10}>
            <ICD10SynonymsSection
              classes={classes}
              icdSynonymAction={icdSynonymAction}
              setICDSynonymAction={(data: any[]) => setICDSynonymAction([...data])}
            />
          </Grid>

        ) : activeItem === 'Doctor Specialization' && activeHeader === 'Codes' ? (
          <Grid item xs={12} md={10}>
            <DoctorSpecializationSection
              classes={classes}
            />
          </Grid>
        ) : activeItem === 'RVS Code' && activeHeader === 'Codes' ? (
          <Grid item xs={12} md={10}>
              <RVSSection
                classes={classes}
              />
          </Grid>
        ) : null}
      </Grid>
    </div>
  );
};

const mapStateToProps = (state: Store) => state.lookup;

const mapDispatchToProps = (dispatch: Dispatch) => ({
  Map: bindActionCreators(LookupActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };
