import { cloneDeep } from 'lodash';
import { SIDE_MODAL_BLOCK_FIELDS } from 'Components/UI/SideModalBlock/FieldConstants';
import moment from 'moment';
import React from 'react';
import { TableComponent } from 'Components/UI/TableComponent/CodeListTable';
import { Search } from '@material-ui/icons/'; 
import { Loader } from '../Loader';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { ConfirmationModal } from '../ConfirmationModal';
import { DoctorSpecialization } from '../../LookupModels';

import {
  constructEmptyDoctorSpecialization,
  constructDoctorSpecialization,
  constructPutDoctorSpecialization,
  constructPostDoctorSpecialization
} from '../ConstructPayloads';
import {
  searchDoctorSpecialization,
  editDoctorSpecialization,
  addDoctorSpecialization
} from 'Pages/API/Calls/DoctorSpecialization';
import {
  TextField,
  InputAdornment,
  Grid,
  Link
} from '@material-ui/core';
import {
  GridHorizontalDivider,
  SideModalBlock,
  SideModalBody,
  MsgModal
} from 'Components/UI';


const DEV_MODE: boolean = false;
const HIDE_EDIT_MODE: boolean = false;

const table_specs: any = {
  formattedColumns: {},
  columnExtensions: [
    {
      columnName: 'code',
      width: 100,
      wordWrapEnabled: true,
    },
    {
      columnName: 'description',
      width: 300,
      wordWrapEnabled: true,
    },
    {
      columnName: 'effectivity_date',
      width: 180,
      wordWrapEnabled: true,
    },
    {
      columnName: 'expiry_date',
      width: 180,
      wordWrapEnabled: true,
    },
    {
      columnName: 'status',
      width: 200,
      wordWrapEnabled: true,
    },
  ],
  columns: [
    {
      name: 'code',
      title: 'Code',
    },
    {
      name: 'description',
      title: 'Description',
    },
    {
      name: 'effectivity_date',
      title: 'Effectivity Date',
      datePicker: true
    },
    {
      name: 'expiry_date',
      title: 'Expiration Date',
      datePicker: true
    },
    {
      name: 'status',
      title: 'Status',
      staticDropdown: true,
      staticDropdownOptions: [
        {
          value: 'all',
          label: 'All',
        },
        {
          value: 'active',
          label: 'Active'
        },
        {
          value: 'deactivated',
          label: 'Deactivated'
        },
        {
          value: 'awaiting activation',
          label: 'Awaiting activation'
        }
      ]
    },
  ],
};



interface IState {

  // confirmation modal essentials
  confirm_modal_title: string;
  confirm_modal_message: string;
  confirm_modal_open: boolean;
  action_after_confirm: () => void;
  confirm_modal_button: string;


  // notif modal essentials
  isModalOpen: boolean;
  modalTitle: string;
  modalMessage: any;


  // side modal essentials
  invalid_fields: string[];
  block_title?: string;
  show_side_modal_block?: string;
  side_modal_block_mode: 'view' | 'edit' | 'add' | '';
  disableSaveButton: boolean;
  clickSaveButton: boolean;
  code_info?: DoctorSpecialization;
  code_response: any;


  // table and page essentials
  code_data: DoctorSpecialization[];
  global_filter: string;
  default_filters: any;
  default_sorting: any;
  totalCount: number;
  typing: boolean;
  typingTimeout: any;
  page: number;
  limit: number;
  isPageSizeModal: boolean;
  loading_state: boolean;
  no_data_message: string;
}

interface IProps {
  classes: any;
}

export class DoctorSpecializationSection extends React.Component<IProps, IState> {
  private requiredFields: any;
  private excludeFields: any;

  constructor(props) {
    super(props);

    this.requiredFields = SIDE_MODAL_BLOCK_FIELDS.DoctorSpecialization.editOrAdd.requiredFields;
    this.excludeFields = SIDE_MODAL_BLOCK_FIELDS.DoctorSpecialization.editOrAdd.excludeFields


    this.state = {
      no_data_message: '',


      // confirmation modal essentials
      confirm_modal_title: '',
      confirm_modal_message: '',
      confirm_modal_open: false,
      action_after_confirm: () => { },
      confirm_modal_button: 'Confirm',


      // notif modal essentials



      // side modal essentials
      invalid_fields: [],
      code_response: [],
      code_info: constructEmptyDoctorSpecialization(),
      show_side_modal_block: 'none',
      side_modal_block_mode: '',
      block_title: '',


      // table and page essentials
      loading_state: true,
      code_data: [],
      global_filter: '',
      default_filters: [],
      default_sorting: [
        {
          columnName: 'code',
          direction: 'asc'
        }
      ],
      isModalOpen: false,
      isPageSizeModal: false,
      modalTitle: '',
      modalMessage: '',
      totalCount: 0,
      typing: false,
      typingTimeout: 0,
      page: 0,
      limit: 10,
      disableSaveButton: false,
      clickSaveButton: false,
    };
  }

  componentDidMount() {
    this.initialData();

    document.addEventListener('keydown', this.escFunction, false);
  }

  escFunction = (event) => {
    if (event.keyCode === 27) {
      this.handleCloseSideModalBlock()
    }
  }

  initialData = () => {
    this.setState({ no_data_message: `Searching for Doctor Specialization Codes` })
    this.searchCode(
      10,
      0,
      this.state.default_filters,
      this.state.global_filter,
      this.state.default_sorting)
  };






































  /***********************  
       CRUD FUNCTIONS 
  ***********************/

  searchCode = (
    limit: number,
    skip: number,
    filters: any,
    filters_global: any,
    sorting: any) => {

    searchDoctorSpecialization(limit, skip, filters, filters_global, sorting).then(response => {
      this.setState({ no_data_message: 'No available data' })
      if (response && response.error === undefined) {
        this.setState({
          code_response: response,
          code_data: this.rowDataFixer(response.result ? response.result : []),
          loading_state: false,
          totalCount: response.total && response.total.count ? response.total.count : 0,
        });
      } else {
        this.setState({
          code_response: [],
          loading_state: false,
          isModalOpen: true,
          modalTitle: 'Loading failed',
          modalMessage: 'An error occured while loading the list of codes.',
        });
      }
    }).catch(e => {
      this.setState({
        no_data_message: 'No available data',
        code_response: [],
        loading_state: false,
        isModalOpen: true,
        modalTitle: 'Loading failed',
        modalMessage: e && e.message ? e.message : 'An error occured while loading the list of codes.',
        page: 0,
      });
    });
  }


  patchCode = (data: any) => {
    const payload: DoctorSpecialization = constructPutDoctorSpecialization(data);

    if (!payload || !payload._id || !payload.code) return;

    this.setState({
      loading_state: true,
    });


    editDoctorSpecialization(JSON.stringify(payload), payload._id).then(response => {
      this.setState({
        loading_state: false,
        no_data_message: 'No available data'
      })
      if (response && response.error === undefined && response.message !== undefined) {
        this.changePageUpdate(this.state.limit, this.state.limit * this.state.page);
        this.setState({
          disableSaveButton: true,
          isModalOpen: true,
          modalTitle: 'Success',
          modalMessage: response.message ? response.message : `Successfully edited a Doctor Specialization code.`
        });
      } else {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Failed',
          modalMessage: 'Something went wrong while saving!'
        });
      }
    }).catch(error => {
      this.createErrorPrompt(error);
    });
  }

  createErrorPrompt = (error: any) => {
    let modalTitle: any = 'Error';
    let modalMessage: any = 'Something went wrong while saving.';
    if (error && error.message) {
      try {
        let parsed_message: any = JSON.parse(error.message);
        modalTitle = parsed_message['title'];
        // modalMessage = parsed_message['message'];
        const bracketIndexStart: number = parsed_message['message'].indexOf('[');
        const bracketIndexEnd: number = parsed_message['message'].indexOf(']');
        if (bracketIndexStart !== -1 && bracketIndexEnd !== -1) {
          let strong_str: string = parsed_message['message'].substring(bracketIndexStart, bracketIndexEnd + 1);
          const first_str: string = parsed_message['message'].split('[')[0];
          let temp: string[] = parsed_message['message'].split(']');
          const end_str: string = temp[temp.length - 1];
          modalMessage = <span>{first_str}<strong>{strong_str}</strong>{end_str}</span>
        } else {
          modalMessage = <span>{parsed_message['message']}</span>
        }
      } catch{
        modalMessage = error.message;
      }
    }
    this.setState({
      isModalOpen: true,
      modalTitle: modalTitle,
      modalMessage: modalMessage,
      loading_state: false
    });
  }


  addCodes = (data: any) => {
    this.setState({
      loading_state: true
    })

    const payload: DoctorSpecialization[] = constructPostDoctorSpecialization(data)
    
    addDoctorSpecialization(JSON.stringify(payload)).then(response => {
      this.setState({
        loading_state: false
      })
      if(response && response.error === undefined && response.message && response.result) {
        this.changePageUpdate(this.state.limit, this.state.limit * this.state.page)
        this.setState({
          disableSaveButton: true,
          isModalOpen: true,
          modalTitle: 'Success',
          modalMessage: response.message ? response.message : `You have successfully added a Doctor Specialization code!`
        })
      } else if (response && Response.error && response.error.statusCode === 401) {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Unauthorized',
          modalMessage: 'You are not authorized to add Doctor Specialization codes.'
        })
      } else if(response && response.error && response.error.statusCode === 400 && response.error.message) {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Doctor Specialization Code Already Exists',
          modalMessage: (<span><strong>[{payload[0].code}]</strong> cannot be added. This already exists on the list.</span>)
        })
      } else {
        this.setState({
          isModalOpen: true,
          modalTitle: 'Failed',
          modalMessage: 'Something went wrong while adding!'
        })
      }
    }).catch(error => {
      this.createErrorPrompt(error);
    });
  }









































  /***********************  
  TABLE AND PAGE FUNCTIONS 
  ***********************/

  rowDataFixer(rows: any) {

    const dateFormatter = (date: Date | string | null | undefined) => {
      if (!date || !moment(date).isValid()) return 'N/A';
      return moment(date).format('LL');
    }

    const titleCase = (title: string) => {
      if (!title) return 'N/A';
      var sentence: string[] = title.toLowerCase().split(" ");
      for (var i = 0; i < sentence.length; i++) {
        sentence[i] = sentence[i][0].toUpperCase() + sentence[i].slice(1);
      }
      const output: string = sentence.join(" ");
      return output;
    }

    let generatedTable: DoctorSpecialization[] = [];

    if (Array.isArray(rows) && rows.length > 0) {
      generatedTable = rows.map(row => {
        if (!row) return constructEmptyDoctorSpecialization();
        const docSpec: DoctorSpecialization = {
          _id: row['_id'],
          code: row['code'] ? row['code'] : 'N/A',
          description: row['description'] ? row['description'] : 'N/A',
          effectivity_date: dateFormatter(row['effectivity_date']),
          expiry_date: dateFormatter(row['expiry_date']),
          status: row['status'] ? titleCase(row['status']) : 'N/A',
          date_updated: row['date_updated'] ? row['date_updated'] : 'N/A',
        };
        return docSpec;
      });
    }

    return generatedTable;
  }







  changePageUpdate = (limit: number, skip: number) => {
    this.handleCloseSideModalBlock();
    this.setState({
      loading_state: true,
      no_data_message: `Searching for Doctor Specialization codes`
    });
    this.searchCode(
      limit,
      skip,
      this.state.default_filters,
      this.state.global_filter,
      this.state.default_sorting)
  };






  handleChangeSorting = (sorting: any) => {
    this.setState({
      default_sorting: sorting,
      loading_state: true,
      no_data_message: `Searching for Doctor Specialization codes`
    });
    if (sorting && sorting.length > 0) {
      this.searchCode(
        this.state.limit,
        this.state.page * this.state.limit,
        this.state.default_filters,
        this.state.global_filter,
        sorting,
      )
    }
  };





  handleFilterByColumn = (filter: any) => {
    const self = this;

    if (self.state.typingTimeout) {
      clearTimeout(self.state.typingTimeout);
    }

    self.setState({
      default_filters: filter,
      typing: false,
      page: 0,
      typingTimeout: setTimeout(function () {
        self.setState({
          loading_state: true,
          no_data_message: `Searching for Doctor Specialization codes`
        });
        self.searchCode(
          self.state.limit,
          0,
          filter,
          self.state.global_filter,
          self.state.default_sorting,
        )
      }, 2000),
    });
  };

  handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target;

    const self = this;

    if (self.state.typingTimeout) {
      clearTimeout(self.state.typingTimeout);
    }
    self.setState({
      global_filter: value,
      typing: true,
      page: 0,
      typingTimeout: setTimeout(function () {
        self.setState({
          loading_state: true,
          typing: false,
          limit: 10,
          no_data_message: `Searching for Doctor Specialization codes`
        });
        self.searchCode(
          self.state.limit,
          0,
          self.state.default_filters,
          self.state.global_filter,
          self.state.default_sorting,
        )

      }, 2000),
    });
  };





  handleClickRow = (row: any) => {

    if (DEV_MODE) return;

    // console.log(row)
    const { code_response } = this.state;
    if (!code_response || !code_response['result'] || !code_response['result'][row]) return;

    const data: DoctorSpecialization = constructDoctorSpecialization(cloneDeep(code_response['result'][row]));
    this.checkFields(data, true)
    this.setState({
      code_info: data,
    }, () => this.handleChangeSideModalBlockMode('view'));
  };





  handlePageChange = (currentPage: number, skip: number) => {
    this.setState({
      page: currentPage,
    });
    this.changePageUpdate(this.state.limit, skip);
  };




  handlePageSizeChange = value => {
    let pageLimit = 100000;

    if (this.state.totalCount >= pageLimit) {
      this.setState({
        isPageSizeModal: true,
      });
    } else {
      this.setState({
        limit: value,
      });

      this.changePageUpdate(value, value * this.state.page);
    }
  };




































  /*****************************  
  ACTIONS / SIDE MODAL FUNCTIONS 
  ******************************/

  handleCloseSideModalBlock = () => {
    this.setState({
      show_side_modal_block: 'none',
      side_modal_block_mode: '',
      code_info: constructEmptyDoctorSpecialization(),
    });
  };





  handleChangeSideModalBlockMode = (value: 'view' | 'edit' | 'add' | '') => {
    let title = ``;
    if (value && value === 'edit') {
      if (HIDE_EDIT_MODE) return;
      title = `Edit ${this.state.block_title}`;
    } else if (value && value === 'view' && this.state.code_info) {
      title = this.state.code_info['code'] ? this.state.code_info['code'] : '';
    } else if (value && value === 'add') {
      title = `Add Doctor Specialization`;
    }
    this.setState({
      side_modal_block_mode: value,
      block_title: title,
    }, this.handleOpenBlock);
  };





  handleOpenBlock = () => {
    this.setState({
      show_side_modal_block: 'block',
    });
  };




  handleInvalidFieldsPrompt = () => {
    this.setState({
      isModalOpen: true,
      modalTitle: 'Incomplete/Invalid Fields',
      modalMessage: 'Please complete the form before proceeding.',
    })
  }




  checkFields = (formFields: any, disableButton?: boolean) => {
    this.setState({ loading_state: true })
    if (formFields) {
      let invalid_fields = this.getInvalidFields(formFields, disableButton);
      if (!invalid_fields || invalid_fields.length === 0) {
        if (this.state.disableSaveButton || !this.state.clickSaveButton) return;


        if (this.state.side_modal_block_mode === 'edit') {
          let message: any = '';
          let title: string = '';
          let button: string = '';
          if (formFields && formFields['expiry_date'] && moment(formFields['expiry_date']).isValid() && moment(new Date()).isSameOrAfter(moment(formFields['expiry_date']).startOf('day'))) {
            title = 'Deactivate Doctor Specialization Code'
            message = <span style={{ display: 'inline-block', width: '300px' }}>Would you like to continue deactivating <b>Doctor Specialization Code [{formFields['code']}]</b>?</span>
            button = 'Deactivate';
          } else {
            button = 'Confirm';
            title = 'Edit Doctor Specialization Code'
            message = <span style={{ display: 'inline-block', width: '300px' }}>Would you like to continue editing <b>Doctor Specialization Code [{formFields['code']}]</b>?</span>
          }
          this.setState({
            clickSaveButton: false,
            confirm_modal_button: button,
            confirm_modal_message: message,
            confirm_modal_open: true,
            confirm_modal_title: title,
            action_after_confirm: () => this.patchCode(formFields)
          })
        } else if (this.state.side_modal_block_mode === 'add') {
          this.setState({
            confirm_modal_button: 'Confirm',
            clickSaveButton: false,
            confirm_modal_message: `Are you sure you want to add this Doctor Specialization code?`,
            confirm_modal_open: true,
            confirm_modal_title: `Confirm add Doctor Specialization code`,
            action_after_confirm: () => this.addCodes(formFields)
          })
        }
      } else {
        this.handleInvalidFieldsPrompt()
      }
    }
  }





  getInvalidFields = (formFields: any, disableButton?: boolean) => {
    let invalid_fields_submit: any[] = [];
    let invalid_fields_editing: any[] = [];
    let fields_excluded: string[] = []
    if (this.excludeFields !== undefined) {
      fields_excluded = this.excludeFields
    }
    // const fields_excluded: string[] = Object.keys(exclude_fields)
    const form_keys: string[] = Object.keys(formFields)

    form_keys.forEach(field => {
      if (this.requiredFields &&
        this.requiredFields.includes(field) &&
        !fields_excluded.includes(field) &&
        !formFields[field]) {
        if (!disableButton) {
          invalid_fields_submit.push(field)
        } else {
          invalid_fields_editing.push(field)
        }
      }
    })

    const eff_date = moment(formFields['effectivity_date'])
    const exp_date = moment(formFields['expiry_date'])
    if (formFields &&
      formFields['effectivity_date'] &&
      formFields['expiry_date'] &&
      eff_date.isValid() &&
      exp_date.isValid() &&
      eff_date.startOf('day').diff(exp_date.startOf('day')) > 0) {
      if (disableButton) {
        invalid_fields_editing.push('effectivity_date')
      } else {
        invalid_fields_submit.push('effectivity_date')
      }
    }

    if (!invalid_fields_editing || invalid_fields_editing.length === 0) {
      this.setState({ disableSaveButton: false })
    } else if (!this.state.disableSaveButton) {
      this.setState({ disableSaveButton: true })
    }
    this.setState({ loading_state: false, invalid_fields: invalid_fields_submit })
    return invalid_fields_submit
  }




































  /***********************  
       RENDER SECTIONS 
  ***********************/

  renderSideModalBlock(): JSX.Element {

    return (
      <SideModalBlock
        blockPosition={'fixed'}
        title={this.state.block_title}
        editIconDescription={'Edit Specialization'}
        showBlock={this.state.show_side_modal_block}
        handleClose={this.handleCloseSideModalBlock}
        sideModalBlockMode={this.state.side_modal_block_mode}
        changeMode={this.handleChangeSideModalBlockMode}

      >
        {this.state.side_modal_block_mode ? (
          <SideModalBody
            showRequiredFields
            verticalSpacing="15px"
            formFields={this.state.code_info}
            invalidFields={this.state.invalid_fields}
            handleClose={this.handleCloseSideModalBlock}
            refreshData={(data: any) => {
              this.setState({ code_info: data },
                () => this.checkFields(data, true))
            }}
            handleSave={(formFields?: any) => {
              if (this.state.side_modal_block_mode) {
                this.setState({ clickSaveButton: true }, () => this.checkFields(formFields))
              }
            }}
            formName={'DoctorSpecialization'}
            mode={this.state.side_modal_block_mode ? this.state.side_modal_block_mode : ''}
            checkBoxWithDropDownFieldsInitState={this.state.code_info && this.state.code_info['gender_limited'] ? true : false}
            disableSaveButton={this.state.disableSaveButton}
          />
        ) : null}
      </SideModalBlock>
    )
  }









  render(): JSX.Element {
    const { classes } = this.props;

    return (
      <div>
        {this.state.loading_state ? <Loader /> : null}
        <Grid container>




          <Grid container style={{ paddingLeft: '37px' }}>
            <Grid item xs={12} md={10}>
              <TextField
                style={{ width: '100%', backgroundColor: 'white' }}
                id="code_search"
                placeholder="Search a code or description"
                label=""
                margin="none"
                fullWidth
                variant="outlined"
                value={this.state.global_filter}
                inputProps={{ 'aria-label': 'bare' }}
                onChange={this.handleSearch}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Search style={{ color: 'rgba(54, 54, 54, 0.4)' }} />
                    </InputAdornment>
                  ),
                }}
                onKeyUp={(event) => {
                  let code = event.keyCode || event.which
                  if (code == 13 && this.state.typing && this.state.global_filter) {
                    if (this.state.typingTimeout) {
                      clearTimeout(this.state.typingTimeout);
                    }
                    this.setState({
                      loading_state: true,
                      typing: false,
                      limit: 10,
                      no_data_message: `Searching for Doctor Specialization codes`
                    });
                    this.searchCode(
                      this.state.limit,
                      0,
                      this.state.default_filters,
                      this.state.global_filter,
                      this.state.default_sorting,
                    )
                  }
                }}
              />
            </Grid>

            <Grid container justify="flex-end" item xs={8} md={2} style={{ paddingRight: '15px', marginTop: 15, textAlign: 'center' }}>
              <FontAwesomeIcon icon={faPlus} className={classes.plusIcon} />
              <Link className={classes.addACode}
                onClick={() => {
                  if (DEV_MODE) return;
                  this.setState(
                    {
                      block_title: `Add Doctor Specialization`,
                      code_info: constructEmptyDoctorSpecialization(),
                      disableSaveButton: true
                    }, () => this.handleChangeSideModalBlockMode('add'));
                }}>
                Add a Code
                  </Link>
            </Grid>






            <GridHorizontalDivider height="30px" />
            <Grid style={{ minHeight: 690, overflow: 'auto' }}>
              <TableComponent
                id="tab-all"
                totalCount={this.state.totalCount}

                rows={
                  Object.keys(this.state.code_data).length > 0
                    ? this.state.code_data
                    : []
                }
                columns={
                  table_specs['columns']

                }
                message={
                  this.state.global_filter
                    ? 'No available data'
                    : this.state.no_data_message
                }
                onClickRow={this.handleClickRow}
                disableSelect
                disableSearch
                formattedColumns={table_specs['formattedColumns']}
                columnExtensions={table_specs['columnExtensions']}
                onFilterChange={this.handleFilterByColumn}
                // onSortingChange={this.handleChangeSorting}
                updateSorting={this.handleChangeSorting}
                // update={this.changePageUpdate}
                update={() => { }}
                disableIntegratedSorting
                disableIntegratedFiltering
                onPageChange={this.handlePageChange}
                ongPageSizeChange={this.handlePageSizeChange}
                defaultFilter={this.state.default_filters}
                currentPage={this.state.page}
                limit={this.state.limit}
                pageSizes={[10, 25, 50, 100]}
              />
            </Grid>
          </Grid>
        </Grid>









        {this.renderSideModalBlock()}

        <MsgModal
          onClose={() => {
            this.setState({
              isModalOpen: false,
            })
          }}
          title={this.state.modalTitle}
          isModalOpen={this.state.isModalOpen}
          message={this.state.modalMessage}
        />
        <ConfirmationModal
          onClose={() => this.setState({ confirm_modal_open: false })}
          title={this.state.confirm_modal_title}
          isModalOpen={this.state.confirm_modal_open}
          submitType={this.state.confirm_modal_button}
          onSubmit={() => {
            if (this.state.action_after_confirm) this.state.action_after_confirm()
          }}
          message={this.state.confirm_modal_message} />
      </div>
    );
  }
}
