import { makeStyles } from '@material-ui/core/styles';
import {
    Button,
    Grid,
    // TextField,
    InputLabel,
    Link,
    CircularProgress
} from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import { GridHorizontalDivider } from 'Components/UI';
import { TableComponent } from 'Components/UI/TableComponent/CodeListTable';
import { getAllSynonyms } from 'Pages/API/Calls/ICD10';
import { Icd10Synonym, Icd10 } from '../../LookupModels'
import { cloneDeep } from 'lodash';

