import Fetch, { requestOption } from '../index'

const fetch = new Fetch();
fetch.getAccessToken();

export const searchDoctorSpecialization = (limit: number, offset: number, filters: any, query: string, sorting?: any) => {
    
    let uri_obj: any = {};
    let uri_string: string = `doctor-specialization/search`;
    let filter = {};
    if (filters && Array.isArray(filters) && filters.length > 0) {
        filter['where'] = {};
        filters.forEach(item => {
            if (item['columnName'] === 'status' && item['value'] === 'all') 
                console.log('do nothing')
            else if (item['columnName'] !== 'effectivity_date' && item['columnName'] !== 'expiry_date')
                filter['where'][item['columnName']] = { like: item['value'], options: 'i' }
            else
                filter['where'][item['columnName']] = item['value']
        });
    }

    if (sorting && Array.isArray(sorting) && sorting.length > 0)
        filter['order'] = `${sorting[0]['columnName']} ${sorting[0]['direction']}`;

    if (offset) filter['skip'] = offset;

    if (limit) filter['limit'] = limit;

    if (query) uri_obj['search'] = query;

    if (Object.entries(filter).length > 0) uri_obj['filter'] = JSON.stringify(filter);

    let i = 0;
    for (let key of Object.keys(uri_obj)) {
        if (!i) uri_string += '?';
        else uri_string += '&';
        uri_string += `${key}=${uri_obj[key]}`;
        i += 1;
    }

    let option: requestOption = {
        method: 'GET'
    }

    if (fetch.getAccessToken()) return fetch.call(option, uri_string, true);
    
    return fetch.fetchAccessToken().then(() => fetch.call(option, uri_string, true))
}


export const editDoctorSpecialization = (payload: any, id: string) => {

    let option: requestOption = {
        method: 'PUT',
        body: payload
    }

    if (fetch.getAccessToken()) return fetch.call(option, `doctor-specialization/edit/${id}`, true);
    
    return fetch.fetchAccessToken().then(() => fetch.call(option, `doctor-specialization/edit/${id}`, true))
}

export const addDoctorSpecialization = (payload: any) => {

    let option: requestOption = {
        method: 'POST',
        body: payload
    }

    if(fetch.getAccessToken()) {
        return fetch.call(option, `doctor-specialization/multiple`, true)
    }

    return fetch.fetchAccessToken().then(() => fetch.call(option, `doctor-specialization/multiple`, true))
}